@import url(https://fonts.googleapis.com/css?family=Roboto);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body {
    font-family: 'Montserrat';
}

html {
    scroll-behavior: smooth;
}

@font-face {
    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 400;
    src: local('Montserrat-Regular'), url(https://fonts.gstatic.com/s/montserrat/v6/zhcz-_WihjSQC0oHJ9TCYC3USBnSvpkopQaUR-2r7iU.ttf) format('truetype');
}

@font-face {
    font-family: 'Montserrat';
    font-style: bold;
    font-weight: 700;
    src: local('Montserrat-Bold'), url(https://fonts.gstatic.com/s/montserrat/v6/IQHow_FEYlDC4Gzy_m8fcvEr6Hm6RMS0v1dtXsGir4g.ttf) format('truetype');
}

h1, h2, h3, h4, h5, h6, p {
    padding: 0px;
    margin: 0px;
}

h1 {
    margin-top: 60px;
    margin-bottom: 20px;
}

h2 {
    margin: 10px 0px;
    margin-top: 30px;
}

.text-center {
    text-align: center;
}

body, html {
    height: 100%;
    margin: 0;
}

.main-container {
    display: flex;
}

.main-cover {
    background-image: linear-gradient(to bottom, rgba(245, 246, 252, 0.52), rgba(0, 0, 0, 1)), url(/static/media/Cover.2b645aeb.jpg);
    background-repeat: no-repeat;
    background-size: auto;
    background-position: top;
    height: 100vh;
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    z-index: -1;
}

.main-title {
    text-align: center;
    margin: 0 auto;
    font-size: 30px;
}

.main-title h1 {
    background: white;
    padding: 20px 100px;
}

.menu-container {
    width: 40%;
    margin: 0 auto;
    margin-top: 170px;
}

.age-box {
    position: relative;
    width: 60%;
    margin: 0 auto;
}

.age-box:before {
    content: "";
    display: block;
    padding-top: 100%;
}

.age h1 {
    margin: 0;
}

.age {
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    background: #1677CB;
    color: white;
    font-size: 50px;
    line-height: 100%;
    height: 100%;
    text-align: center;
    display: flex;
    align-items: center;
    justify-content: center;
}

.film-playlist {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
}

.embed-container {
    position: relative;
    padding-bottom: 56.25%;
    height: 0;
    overflow: hidden;
    max-width: 100%;
}

.embed-container iframe, .embed-container object, .embed-container embed {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
}

.menu-item {
    background: linear-gradient(to right, #1677CB, white);
    color: black;
    padding: 30px;
    margin: 20px;
    flex: 1 1 auto;
    margin: 20px;
    padding: 30px;
    text-align: center;
    text-transform: uppercase;
    transition: 0.4s;
    background-size: 200% auto;
}

.menu-item:hover {
    background-position: right center;
    cursor: pointer;
}

.menu-item p {
    font-size: 25px;
}

.App {
    width: 80%;
    margin: 0 auto;
}

.header {
    margin-top: 70px;
}

.header-img-container {
    position: relative;
    height: 250px;
    width: 250px;
    border-radius: 100%;
    margin: 0 auto;
}

.header-img-container:hover, .art-nav-items a:hover, .art-header-img:hover, .pointer {
    cursor: pointer;
}

.header-img {
    display: block;
    height: 250px;
    width: 250px;
    object-fit: cover;
    border-radius: 100%;
    margin: 0 auto;
    margin-top: 20px;
    border: solid 5px #1677CB;
    background-image: linear-gradient(white, white), radial-gradient(circle at top left, #F012BE, #39CCCC);
    background-origin: border-box;
}

.name-intro {
    border: 5px solid;
    border-color: #1677CB white #8E8E8E;
    margin-top: 70px;
}

.name-intro h1 {
    font-size: 50px;
    margin: 25px;
}

.art-header {
    display: flex;
    box-shadow: 0px 0px 10px 0px #3F3F3F;
}

.art-header-content h1 {
    float: left;
    margin-left: 20px;
    margin-top: 30px;
    text-transform: uppercase;
    font-size: 45px;
}

.art-header-content {
    width: 80%;
    margin: 0 auto;
    display: block;
}

.art-header-img {
    height: 75px;
    width: 75px;
    float: left;
    border: none;
    margin: 20px 0px;
}

.header-name {
    font-size: 40px;
    text-align: center;
    margin-top: 30px;
}

.header-bio {
    font-weight: 500;
    text-align: center;
    margin-top: -15px;
    margin-bottom: 10px;
}

nav {
    overflow: hidden;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    flex-flow: wrap;
    max-width: 400px;
    margin: 0 auto;
}

.art-nav {
    overflow: hidden;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    flex-flow: wrap;
    margin: 0 auto;
    margin-top: 40px;
    float: right;
}

.checkbtn {
    cursor: pointer;
}

.art-mobile {
    display: none;
}

.art-mobile-items a {
    display: block;
    font-weight: 600;
    color: black;
    text-align: center;
    padding: 20px 30px;
    text-transform: uppercase;
    text-decoration: none;
    font-size: 17px;
    margin: 0 auto;
    border-bottom: 3px solid white;
}

nav a, .art-nav a {
    float: left;
    display: block;
    font-weight: 600;
    color: black;
    text-align: center;
    padding: 10px 10px;
    text-decoration: none;
    font-size: 17px;
    margin: 0 auto;
    border-bottom: 3px solid white;
    text-transform: uppercase;
}

nav a:hover, .art-nav a:hover {
    color: #8E8E8E;
    border-bottom: 3px solid #8E8E8E;
    transition: border 0.5s;
}

nav a.active, .art-nav a:active {
    color: black;
    border-bottom: 3px solid black;
    transition: border 0.5s;
}

.content {
    padding: 16px;
}

.sticky {
    position: fixed;
    top: 0;
    width: 100%;
}

.about-list-main {
    display: flex;
}

.about-list {
    display: inline-block;
    ;
    margin: 0 auto;
}

.flex-container, .social-container {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    flex-flow: wrap;
}

.social-container {
    width: 80%;
    margin: 0 auto;
    margin-top: 40px;
}

.skill-title, .stack-title, .project-title {
    margin: 0px -10px;
    margin-top: -10px;
    margin-bottom: 20px;
    padding: 15px 5px;
}

.project-title {
    background: #181818;
    color: white;
    padding: 5px 5px;
    display: block;
    position: relative;
}

.project-description {
    text-align: center;
    margin-top: 20px;
}

.project-date {
    text-align: center;
    font-size: 20px;
    margin-top: 20px;
}

.github-icon {
    float: right;
    position: absolute;
    right: 10px;
    top: 10px;
    width: 40px;
}

.skill-box, .stack-box, .project-box {
    background: white;
    width: 20%;
    padding: 10px;
    margin: 10px;
    box-shadow: 0px 0px 5px 0px #3F3F3F;
    transition: box-shadow 0.3s ease;
}

.logo-box {
    width: 20%;
    margin: 20px;
}

.icon-box {
    width: 10%;
    margin: 20px;
}

.logo-box:hover {
    cursor: pointer;
}

.logo-image {
    width: 120%;
    height: 100%;
}

.stack-box {
    background: #DDD;
    width: 10%;
}

.project-box, .welcome-box {
    width: 45%;
}

.welcome-box {
    padding: 10px;
    margin: 10px;
}

.welcome-box p {
    padding: 10px;
}

.welcome-box li {
    margin-left: 15px;
    padding: 5px;
}

.welcome-image {
    object-fit: cover;
    object-position: 0 0;
    overflow: hidden;
    width: 100%;
    height: 600px;
}

.social-box {
    width: 10%;
    margin: 20px 0px;
}

.skill-box:hover, .stack-box:hover, .project-box:hover {
    box-shadow: 0px 0px 5px 3px #3F3F3F;
}

.skill-image, .social-image {
    width: 60%;
    display: flex;
    margin: 0 auto;
}

.social-image {
    max-width: 75px;
}

.footer {
    background: #8E8E8E;
    padding: 10px;
    margin-top: 50px;
}

.footer p {
    color: white;
}

.button {
    width: 150px;
    margin: 10px auto;
    margin-top: 20px;
    padding: 10px;
    border-radius: 50px;
    color: white;
    text-align: center;
    background-color: #8E8E8E;
    transition: background-color 0.2s ease;
}

.button:hover {
    background: #181818;
    cursor: pointer;
}

.project-modal {
    margin: 20px;
}

.project-tech {
    display: flex;
}

.project-tech-box {
    margin: 0 auto;
}

.resume-section {
    margin-top: 40px;
    border-bottom: 3px solid black;
}

.resume-parts p, .bullet-points li {
    padding: 5px 0px;
}

.website-link {
    color: #1677CB;
}

.bullet-points {
    margin-left: 40px;
}

.left-element {
    float: left;
}

.right-element {
    float: right;
}

.clear {
    clear: both;
}

.project-landing {
    width: 60%;
    display: flex;
    margin: 0 auto;
    margin-top: 100px;
    margin-bottom: 60px;
}

#rant-image-project {
    width: 80%;
}

.bullet-points-general {
    margin-left: 20px;
}

@media screen and (max-width: 1200px) {
    .skill-box, .logo-box {
        width: 40%;
    }
    .stack-box, .social-box {
        width: 25%;
    }
    .icon-box {
        width: 15%;
    }
}

@media screen and (max-width: 1000px) {
    .project-box, .welcome-box {
        width: 90%;
    }
}

@media screen and (max-width: 950px) {
    .main-title h1 {
        padding: 20px;
    }
    .art-mobile {
        display: block;
        margin-top: -10px;
    }
    .art-nav-items {
        display: none;
    }
}

@media screen and (max-width: 700px) {
    #rant-image-project {
        width: 100%;
    }
}

@media screen and (max-width: 600px) {
    .skill-box, .stack-box, .logo-box {
        width: 90%;
    }
    .social-box, .icon-box {
        width: 40%;
    }
    .menu-container {
        width: 300px
    }
}

@media screen and (max-width: 475px) {
    .art-header-content h1 {
        font-size: 30px;
        margin-top: 40px;
        margin-left: 10px;
    }
    .project-landing {
        width: 100%;
    }
}

@media screen and (max-width: 400px) {
    .main-title {
        width: 100%;
    }
    nav {
        max-width: 100px;
    }
    .header-img, .header-img-container {
        width: 200px;
        height: 200px;
    }
}

@media screen and (max-width: 375px) {
    .art-header-content h1 {
        display: none;
    }
}

@media screen and (max-width: 270px) {
    .header-img, .header-img-container {
        width: 140px;
        height: 140px;
    }
}
.search-back-button:hover, .user-network:hover, .rant-credits-img:hover, .rant-user-name:hover, .header-img:hover, .comment-credit-text:hover {
    cursor: pointer;
}

.rant-body {
    width: 80%;
    margin: 0 auto;
    max-width: 1200px;
    margin-top: 20px;
    display: block;
    box-shadow: 0px 0px 5px 0px #3F3F3F;
    transition: box-shadow 0.3s ease;
}

.rant-body:hover {
    box-shadow: 0px 0px 5px 3px #3F3F3F;
}

.rant-title {
    background-color: #F012BE;
    display: inline-flex;
    width: 100%;
    position: relative;
}

.rant-title-text {
    float: left;
    padding-left: 20px;
    margin-top: -10px;
    color: white;
    min-height: 65px;
    margin-bottom: -10px;
    margin-right: 35px;
}

.rant-title-text h1 {
    font-weight: bold;
}

.more-icon {
    float: right;
    position: absolute;
    right: 0px;
    top: 5px;
}

.clear {
    clear: both;
}

.rant-credits {
    width: auto;
    margin: 0 auto;
    margin-top: 20px;
    margin-right: 20px;
    margin-left: 20px;
    height: 110px;
    position: relative;
}

.rant-credits-img img {
    width: 100px;
    height: 100px;
    object-fit: cover;
    border-radius: 100px;
    float: left;
    border: solid 5px transparent;
    background-image: linear-gradient(white, white), radial-gradient(circle at top left, #F012BE, #39CCCC);
    background-origin: border-box;
    background-clip: content-box, border-box;
}

.rant-credits-info {
    float: left;
    margin-left: 20px;
    font-style: normal;
    margin-top: -10px;
}

.user-handle-hover {
    margin-top: -20px;
    color: #39CCCC;
    transition: .2s ease;
    cursor: pointer;
}

.user-handle-hover:hover, .user-network {
    color: #F012BE;
}

.rant-credits-info h3 {
    margin-top: -15px;
}

.rant-credits-info p {
    margin-top: 5px;
}

.rant-credits-date {
    position: absolute;
    right: 0;
    top: 0;
    text-align: right;
    margin-top: 25px;
}

.rant-credits-date p {
    margin-top: 3px;
}

.rant-content {
    margin: 20px;
    padding-bottom: 20px;
}

.rant-content p {
    overflow-wrap: break-word;
}

.rant-info {
    margin-top: 20px;
    display: inline-block;
    width: 100%;
}

.rant-info span {
    width: 20px;
    margin-right: 30px;
    margin-left: 5px;
    font-weight: 600;
}

.rant-like-icon {
    margin-right: -40px;
}

.main-home-content {
    display: block;
    width: 80%;
    margin: 0 auto;
    max-width: 1200px;
}

.user-rant-date {
    float: right;
}

.search-bar {
    display: flex;
    margin: 0 auto;
    margin-top: 30px;
    width: 80%;
    max-width: 1200px;
    box-shadow: 0px 2px 10px 3px rgba(0, 0, 0, 0.25);
}

.search-query {
    padding: 0px 40px;
    margin-top: -30px;
    margin-bottom: 20px;
    text-align: center;
}

@media screen and (max-width: 700px) {
    .rant-body, .search-bar {
        width: 100%;
    }
    .rant-credits-img img {
        width: 100px;
        height: 100px;
        border: solid 3px transparent;
        position: relative;
        display: block;
        margin: 0 auto;
        margin-bottom: 20px;
        float: none;
    }
    .rant-content-body {
        width: 100%;
    }
    .rant-credits-info {
        width: 100%;
        margin: 0 auto;
        margin-top: -20px;
        text-align: center;
    }
    .rant-credits {
        margin-top: 10px;
        margin-right: 10px;
        margin-left: 10px;
        display: block;
    }
    .rant-credits-main {
        display: block;
    }
    .rant-credits-date {
        width: 100%;
        text-align: center;
        float: left;
        position: relative;
        margin-top: 20px;
        margin-bottom: 20px;
    }
    .rant-title-text {
        padding-left: 10px;
    }
    .more-icon {
        right: -5px;
    }
}

@media screen and (max-width: 400px) {
    .rant-info span {
        margin-right: 10px;
        margin-left: 5px;
        font-weight: 600;
    }
}
.edit-name, .edit-price {
    margin-top: 10px;
    margin-left: 20px;
    font-size: 1.5em;
    background-color: #FF2929;
    border: none;
    border-bottom: 4px solid #FFDC00;
    margin-bottom: 35px;
}

.edit-name {
    float: left;
    width: 40%;
}

.edit-price {
    float: right;
    width: 20%;
    text-align: right;
}

.edit-color {
    background-color: #FF2929;
    border: none;
    border-bottom: 4px solid #FFDC00;
    width: 100%;
    margin-bottom: -5px;
}

.item {
    background-color: #FF2929;
    border-radius: 30px;
    padding: 0px;
    margin: 50px 0px;
    display: flex;
    border: 5px solid black;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
    transition: box-shadow 0.2s ease;
}

.item:hover {
    box-shadow: 0 0 40px 0 rgba(0, 0, 0, 0.7);
}

.content {
    width: 75vw;
    margin: 15px auto;
    padding: 15px;
    display: inline-block;
}

.primary-info {
    margin-top: -10px;
    width: auto;
}

.item-rank {
    background-color: #FFDC00;
    padding: 5px;
    border-radius: 100px;
    width: 50px;
    height: 50px;
    float: left;
}

.item-rank h1 {
    text-align: center;
    margin-top: 6px;
}

.item-info {
    display: block;
}

.item-name {
    margin-top: 10px;
    margin-left: 20px;
    float: left;
}

.item-price {
    margin-top: 10px;
    float: right;
}

.clear-primary {
    clear: both;
}

.secondary-info {
    margin-top: 10px;
    width: 100%;
    display: inline-flex;
    margin-bottom: -30px;
}

.secondary-table {
    float: left;
    width: 100%;
    padding-bottom: 20px;
}

.button-array {
    float: right;
    margin-right: -12px;
    margin-top: -20px;
}

th, td {
    padding-top: 10px;
    padding-bottom: 10px;
}

.update-button {
    border: none;
    height: 40px;
    width: 120px;
    margin-top: 10px;
    border-radius: 40px;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

.list-button, .edit-button {
    border: none;
    height: 40px;
    width: 40px;
    margin: 10px;
    border-radius: 40px;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
    background-color: white;
    transition: background-color 0.2s ease;
}

.list-button:hover {
    cursor: pointer;
    background: #FFDC00;
}

.update-button:hover, .list-button:hover, .edit-button:hover {
    box-shadow: 0 0 40px 0 rgba(0, 0, 0, 0.7);
}

.in-button {
    margin-top: 2px;
}

.tabs {
    background-color: #CCCCCC;
    height: 50px;
    margin: 0 auto;
    margin-top: 40px;
    width: 70%;
    border-radius: 20px;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

.tab-list {
    border: none;
    display: flex;
}

.tab {
    border: none;
    width: 20%;
    list-style: none;
    margin-top: 8px;
}

.sort-select, .sort-not-selected {
    height: 40px;
    width: 40px;
    border-radius: 40px;
    margin: 0 auto;
    margin-top: -5px;
}

.sort-select {
    background-color: grey;
}

.sort-not-selected:hover {
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

.sort-not-selected {
    background-color: #ADADAD;
}

.in-div {
    margin-top: 8px;
    margin-left: 8px;
}

@media screen and (max-width: 650px) {
    .item-name, .item-price {
        font-size: 30px;
    }
    .secondary-info {
        display: block;
    }
    .secondary-table {
        float: left;
    }
    .button-array {
        float: none;
        margin: 0 auto;
    }
}

@media screen and (max-width: 450px) {
    .tabs {
        width: 100%;
    }
}
